
import * as types from '../types'

const state = {

  //loading是否显示
  showLoading:true
}

const actions = {
  /**
   * loading显示
   */
  USER_LOADING_STATE( { commit }, showLoading ){
    commit(types.USER_LOADING_STATE, showLoading)

  }

}

const getters = {
  showLoading:state =>state.showLoading
}

const mutations = {
  [types.USER_LOADING_STATE] ( state, showLoading){
    state.showLoading = showLoading

  }

}

export default {
  state,
  actions,
  getters,
  mutations
}




